import ShopList from '../../component/index/ShopList.vue'
import SocllBut from '../../component/commont/SocllBut.vue'
export default {
	components: {
		ShopList,
		SocllBut
	},
	data() {
		return {
			contentText: {
				contentdown:'上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			cateMaskState: 0, //分类面板展开状态
			headerPosition:"fixed",
			headerTop:"0px",
			filterIndex: 0, 
			cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			goodsList: [], // 搜到的的商品
			keyWork:'' ,// 要搜索的商品关键词
			pddSort:5,
			scrollTop:false,
			loadding:false,
			pageIndex:1,
			pullUpOn:true,
			sort:0 //  排序编号，2，销量，0是综合，4低价，6高佣金，1最新
		};
	},
	onPageScroll: function(e) {
		// 监听滑动
		console.log('huadong')
		this.scrollTop = e.scrollTop > 300;
	},
	onLoad(options){
		this.keyWork = options.keyWork;
		this.loadData()




	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		console.log('下拉刷新，重新加载')
	},
	//加载更多
	onReachBottom(){
		console.log('加载到底部了')
		this.loadding = true;
		if (this.pageIndex == 10) {
			this.loadding = false;
			this.pullUpOn = false
		} else {
			this.pageIndex += 1;
			this.loadData()
			this.loadding = false
		}
		

	},
	methods: {
		//加载商品 ，带下拉刷新和上滑加载
		loadData() {
			// 获取关键字搜素的数据
			let url = `api/supersearch/apikey/maxd/keyword/${this.keyWork}/back/20/sort/${this.sort}/min_id/${this.pageIndex}/is_coupon/1/tb_p/${this.pageIndex}`
			this.$fly.get(url).then(res=>{
				const {data,code,min_id,msg} = res.data
				console.log(url)
				if (data.length >0){
					// 对数据进行处理一下，改成自己需要的样式，
					const GoodList = this.$tol.shop_data(data)
					this.goodsList.push(...GoodList)
				}	
			})
		},
		//筛选点击
		tabClick(index){
			if (this.filterIndex === index && index !==4) return;
			this.filterIndex = index
			this.sort = index
			if (this.filterIndex ===4){
				this.priceOrder =this.priceOrder ===1?2:1
				if (this.priceOrder === 1) this.sort =1 
			}else{
				this.priceOrder =0
			}
			// 加载数据，根据索引加载不同的数据
			this.goodsList=[]
			this.loadData()
				
		},
		//显示分类面板
		toggleCateMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(()=>{
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item){
			this.cateId = item.id;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(item){
			//测试数据没有写id，用title代替
			let id = item.title;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		stopPrevent(){},
		topScrollTap(){
			// 回顶部按钮
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 500
			});
		}
	},
}
